.form-group {
  display: block;
  margin-bottom: 15px;
}

.form-group input {
  padding: 0;
  height: auto;
  height: initial;
  width: auto;
  width: initial;
  margin-bottom: 0;
  display: none;
  cursor: pointer;
}

.form-group label, .wrapper-mcq label {
  position: relative;
  cursor: pointer;
}

.form-group label:before {
  content:'';
  -webkit-appearance: none;
  background-color: transparent;
  /* border: 2px solid #0079bf; */
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05), inset 0px -15px 10px -12px rgba(0, 0, 0, 0.05);
  padding: 10px;
  display: inline-block;
  position: relative;
  vertical-align: middle;
  cursor: pointer;
  margin-right: 5px;
}

.form-group input:checked + label:after {
  content: '';
  display: block;
  position: absolute;
  top: 8px;
  left: 13px;
  width: 7px;
  height: 17px;
  border: solid green;
  border-width: 0 3px 3px 0;
  transform: rotate(45deg);
}

.wrapper-mcq-form [type="radio"]:checked,
.wrapper-mcq-form [type="radio"]:not(:checked), 
.wrapper-mcq-form [type="checkbox"]:checked,
.wrapper-mcq-form [type="checkbox"]:not(:checked){
  position: absolute;
  left: -9999px;
}

.wrapper-mcq-form [type="radio"]:checked + label,
.wrapper-mcq-form [type="radio"]:not(:checked) + label,
.wrapper-mcq-form [type="checkbox"]:checked + label,
.wrapper-mcq-form [type="checkbox"]:not(:checked) + label
{
  position: relative;
  padding-left: 28px;
  cursor: pointer;
  line-height: 20px;
  display: inline-block;
  color: #000;
  border: 1px solid #ddd;
  width: 100%;
  margin-bottom: 8px;
  padding: 10px;
  text-align: left;
  box-shadow: -1px 2px 9px 0px #ddd;
}

.wrapper-mcq-form [type="radio"]:checked + label:after,
.wrapper-mcq-form [type="radio"]:not(:checked) + label:after,
.wrapper-mcq-form [type="checkbox"]:checked + label:after,
.wrapper-mcq-form [type="checkbox"]:not(:checked) + label:after {
  content: '';
  width: 100%;
  height: 100%;
  background: transparent;
  position: absolute;
  top: 0;
  left: 0;
  transition: all 0.2s ease;
  border: 2px solid;
  color: #2e90e3;
}

.wrapper-mcq-form [type="radio"]:not(:checked) + label:after,
.wrapper-mcq-form [type="checkbox"]:not(:checked) + label:after {
  opacity: 0;
  transform: scale(0);
}

